import React, { Component } from 'react'
import moment from 'moment'
import ActivityTable from './activities'
class Day extends Component {
  constructor(props) {
    super(props)
    this.state = {
      selecting: false,
      horainicial: 6,
      selected: [],
      changed: [],
      actividades: [],
      actividad: ''
    }
    //  this.renderHoras = this.renderHoras.bind(this)
  }

  _toggleSelect(hora) {
    if (this.state.changed.includes(hora)) return false
    let changed = this.state.changed
    changed.push(hora)
    this.setState({ changed })

    if (!this.state.selected.includes(hora)) {
      this.setState({ selected: [...this.state.selected, hora] })

      if (this.state.actividades[hora]) {
        console.log(
          'hay actividad para hora ' + hora,
          this.state.actividades[hora]
        )
      } else {
        console.log('NO actividad para hora ' + hora)
      }

      if (this.state.actividades[hora] !== this.state.actividad) {
        let actividades = this.state.actividades

        actividades[hora] = this.state.actividad

        this.setState({ actividades })
      }
    } else {
      let actividades = this.state.actividades

      delete actividades[hora]

      this.setState({ actividades })

      var array = [...this.state.selected] // make a separate copy of the array
      var index = array.indexOf(hora)
      array.splice(index, 1)
      this.setState({ selected: array })

      // if (this.state.actividades[hora]) {
      //   if (this.state.actividad !== this.state.actividades[hora].nombre) {
      //     console.log(
      //       'Es distinto !! ',
      //       this.state.actividad,
      //       this.state.actividades[hora].nombre
      //     )
      //     this.setState({
      //       actividad: this.state.actividades[hora].nombre
      //     })
      //   }
      // }
    }
  }

  _handleMouseDown(e, hora) {
    console.log('mouse down!')

    if (this.state.actividad === '') {
      var d = document.getElementById('activityInput')
      d.className += ' is-invalid'

      return false
    }
    this.setState({ selecting: true })

    this._toggleSelect(hora)
  }
  _handleMouseOver(e, hora) {
    if (!this.state.selecting) {
      console.log('false xq not selecting')
      return false
    }
    if (hora) {
      console.log('mouse over this', hora)
      this._toggleSelect(hora)
    } else {
      console.log('ay caramba')
    }
  }
  _handleMouseUp() {
    this.setState({ selecting: false, changed: [] })

    console.log('mouse up!!', this.state.selected)
  }
  _handleMouseLeave() {
    if (!this.state.selecting) return false
    this.setState({ selecting: false, changed: [] })
    console.log('mouse leave!!', this.state.selected)
  }

  handleText(e) {
    this.setState({ actividad: e.target.value })
  }

  saveActivities() {
    console.log(
      'Saving activities ',
      this.state.actividad,
      this.state.actividades
    )
  }

  renderHoras() {
    let horas = []

    for (let i = 0; i <= 23; i++) {
      let hora_actual = this.state.horainicial + i
      let am_pm = hora_actual > 11 ? 'PM' : 'AM'
      hora_actual = hora_actual > 23 ? hora_actual - 24 : hora_actual

      var horaStyle = {
        background: 'rgb(37, 105, 161)'
      }

      if (this.state.selected.includes(hora_actual)) {
        horaStyle['background'] = 'rgb(137, 105, 161)'
      }

      horas.push(
        <div className="hora">
          <div
            className="actividad"
            style={horaStyle}
            onMouseDown={e => this._handleMouseDown(e, hora_actual)}
            onMouseOver={e => this._handleMouseOver(e, hora_actual)}
          >
            <p>{this.state.actividades[hora_actual] || ''}</p>
          </div>
          <div
            className="horatxt"
            style={horaStyle}
            onMouseDown={e => this._handleMouseDown(e, hora_actual)}
            onMouseOver={e => this._handleMouseOver(e, hora_actual)}
          >
            <p>
              {hora_actual} <br />
              <small>HS</small>
            </p>
          </div>
        </div>
      )
    }

    return horas
  }

  render() {
    return (
      <div id="dia">
        {/* <div className="fecha">{moment().format('LL')}</div> */}
        <div
          style={{ textShadow: 'none' }}
          id="activityInput"
          class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label"
        >
          <input
            style={{ fontSize: '2em', fontWeight: 300, textShadow: 'none' }}
            id="task"
            value={this.state.actividad}
            onChange={e => this.handleText(e)}
            class="mdl-textfield__input"
            type="text"
          />
          <label
            style={{ textShadow: 'none' }}
            class="mdl-textfield__label"
            for="sample4"
          >
            What's on your mind for tomorrow?
          </label>
          <span
            class="mdl-textfield__error is-invalid"
            style={{ textShadow: 'none' }}
          >
            Please enter an activity first
          </span>
        </div>
        <br />
        <div
          className="horas"
          onMouseOver={e => this._handleMouseOver(e)}
          onMouseUp={() => this._handleMouseUp()}
          onMouseLeave={() => this._handleMouseLeave()}
        >
          {this.renderHoras()}
        </div>

        <div class="debug">
          Activities<hr />
          {/* {JSON.stringify(this.state.actividades)} */}
        </div>
      </div>
    )
  }
}

export default Day



